
import { google } from 'googleapis';
import { NextResponse } from 'next/server';

export async function POST() {
  try {
    const { 
        GOOGLE_SHEETS_CLIENT_EMAIL: client_email, 
        GOOGLE_SHEETS_PRIVATE_KEY: private_key_raw,
    } = process.env;

    const FOLDER_ID = '1o7n4kRHV8xXKRij54gg-vbSq6S9H_76L';

    if (!client_email || !private_key_raw || !FOLDER_ID) {
        throw new Error('Missing Google Drive credentials or Folder ID in environment variables.');
    }

    const private_key = private_key_raw.replace(/\\n/g, "\n");

    const auth = new google.auth.GoogleAuth({
      credentials: {
        client_email,
        private_key,
      },
      scopes: ["https://www.googleapis.com/auth/drive.file"],
    });

    const drive = google.drive({ version: "v3", auth });

    const fileMetadata = {
      name: "hello.txt",
      parents: [FOLDER_ID],
    };

    const media = {
      mimeType: "text/plain",
      body: "This is a test file uploaded from DATGenie ðŸš€",
    };

    const file = await drive.files.create({
      requestBody: fileMetadata,
      media: media,
      fields: "id, name, webViewLink",
    });

    return NextResponse.json({
        message: "File created successfully",
        file: file.data,
      });

  } catch (error: any) {
    console.error('Error in /api/drive-test:', error);
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}
